import React, {useEffect, useState} from 'react';
import { 
  useHistory,
  useParams
} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let Box = styled.div`
  padding: 20px;
`;

let Titleh4 = styled.h4`
  font-size : 25px;
  color : ${props => props.color}
`;

// 컴포넌트 개발
// 상세 페이지  컴포넌트
function Detail(props){
  
  let [alert,alert변경] = useState(true);
  let [inputData,inputData변경] = useState('');

  useEffect(()=>{
    console.log("useEffect");
    let timer = setTimeout(() => {
      alert변경(false);
    }, 2000);

    return ()=>{ clearTimeout(timer)}
  },[]);

  let {id} = useParams();
  let history = useHistory();

  var index = props.shoes.findIndex(function(item){ 
    return item.id == id
  });

  var findGood = props.shoes.find(x => x.id == id);

  // console.log(index,"findGood",findGood);
  return(
    <div className="container">
      <Box>
        <Titleh4 className="red">상세 페이지</Titleh4>
      </Box>

      <input onChange={(e)=>{ inputData변경(e.target.value) }} />
{
  alert === true
  ?
    (<div className="my-alert2"> 
        <p>재고가 얼마 남지 않았습니다.</p>
     </div>)
  : null
}     
      <div className="row">
        <div className="col-md-6">
          <img src={'https://codingapple1.github.io/shop/shoes'+ (findGood.id +1) +'.jpg'} width='100%' alt='' border='0' />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{findGood.title}</h4>
          <p>{findGood.content}</p>
          <p>{findGood.price}원</p>
          <Stock stock={props.stock}/>
          <button className="btn btn-danger" onClick={()=>{
            props.stock변경([9,10,11]);
          }}>주문하기</button>  
          <button className="btn btn-danger" onClick={()=>{
            history.goBack();
          }}>뒤로가기</button> 
        </div>
      </div>
    </div> 
  )
}

function Stock(props){
  return (
    <p>재고 : {props.stock[0]}</p>
  )
}

export default Detail;