import React, {useState} from 'react';
// import logo from './logo.svg';
// import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import {
  Navbar,
  Nav,
  NavDropdown,
  // Form,
  Button,
  // FormControl,
  Jumbotron
} from 'react-bootstrap';

import shoesData from './data';
import { 
  Link, 
  Route, 
  Switch 
} from 'react-router-dom';
import axios from 'axios';
import Detail from './Detail';

function App() {

  let [shoes, shoes변경] = useState(shoesData);
  let [stock, stock변경] = useState([10,11,12]);

  return (
    <div className="App">      
      <Navbar bg="light" expand="lg">
        <Navbar.Brand href="#home">ShoeShop</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar> 
      <Switch>
        <Route exact path="/">
          <Jumbotron className="background">
            <h1>20% 시즌 오프</h1>
            <p>
              This is a simple hero unit, a simple jumbotron-style component for calling
              extra attention to featured content or information.
            </p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </Jumbotron>
          <div className="container">
            <div className="row">
              {
                shoes.map(function(shoe,i){
                  return (<Goods shoe={shoe} index={i} key={i} />)
                })
              }
            </div>
            <button className="btn btn-primary" onClick={()=>{
              axios.get('https://codingapple1.github.io/shop/data2.json')
              //ajax 가 성공했을 경우
              .then((result)=>{
                // console.log(뉴어레이);
                // var newArray = [...shoes];
                // // newArray.unshift(result.data);
                // // console.log("성공했어요",result.data,newArray);
                // newArray.concat(result.data);
                // console.log(newArray);

                // // console.log(newArray);
                // shoes변경(newArray);
                shoes변경([...shoes,...result.data]);
              })
              //ajax 가 실패했을  경우
              .catch(()=>{
                console.log("실패했어요");
              })
            }}>더 보기</button>
          </div>
        </Route>
        
        <Route path="/detail/:id">
          <Detail shoes={shoes} stock={stock} stock변경={stock변경}/>
        </Route>
      </Switch>
    </div>
  );
}

// 컴포넌트 개발
// 상품 리스트 컴포넌트
function Goods(props){
  return (      
    <div className="col-md-4">
      <a href=''>
        <img src={'https://codingapple1.github.io/shop/shoes'+ (props.index+1) +'.jpg'} width='100%' alt='' border='0' />
      </a>
      <h4>{props.shoe.title}</h4>
      <p>{props.shoe.content} {props.shoe.price}</p>
    </div>
  )
}

export default App;
